export default function dashboardReducer(state, action) {
    switch (action.type) {
        case 'open_form':
            return {
                ...state,
                isOpen: true
            }
        case 'close_form':
            return {
                ...state,
                isOpen: false
            }
        case 'update_success':
            return {
                ...state,
                isOpen: false,
                successMsg: 'Password updated successfully!',
                errorMsg: ''
            }
        case 'update_failed':
            return {
                ...state,
                errorMsg: action.payload
            }
        default:
            throw new Error();
    }
}