import React, { useState, useReducer } from "react";
import { Form, FormGroup, Label, Input, Button, FormText, } from "reactstrap";
import dashboardReducer from "../reducer/dashboardReducer";
import { updatePwd } from "../api";

function UpdatePwd(props) {

    const [password, setPassword] = useState('')
    const [cnfPassword, setCnfPassword] = useState('')
    const [invalid, setInvalid] = useState(true)

    const handlePwdChange = (e) => {
        setPassword(e.target.value)
    }
    const handleCnfPwdChange = (e) => {
        setCnfPassword(e.target.value)
    }
    const handleError = (e) => {
        if ((password.length < 8) || (password !== cnfPassword)) {
            setInvalid(true)
        }else {
            setInvalid(false)
        }
    }
    const handleClick = (e) => {
        e.preventDefault()
        props.handleUpdate(password)
    }
    return(
        <>
            <Form className="col-lg-6 py-3 bg-dark text-light">
                <FormGroup>
                    <Label for="password">Password</Label>
                    <Input type="password" name="password" placeholder="Enter password" 
                        value={password} 
                        onChange={handlePwdChange}
                        onKeyUp={handleError} />
                    <FormText>Password length must be greater than 8 !</FormText>
                </FormGroup>
                <FormGroup>
                    <Label for="password">Confirm Password</Label>
                    <Input type="password" name="cnf-password" 
                        placeholder="Re-enter password" 
                        value={cnfPassword} 
                        onChange={handleCnfPwdChange}
                        onKeyUp={handleError}
                        />
                    <FormText>Must be same as password !</FormText>
                </FormGroup>
                <Button type="submit" disabled={invalid} color="primary" size="sm" onClick={handleClick}>Update</Button>
                <Button className="ml-3" outline color="secondary" size="sm" onClick={(e) => {e.preventDefault(); props.dispatch({type: 'close_form'}) }}>Cancel</Button>
            </Form>
        </>
    )
}

const initialState = {
    isOpen: false,
    successMsg: '',
    errorMsg: '',
}
export default function Dashboard(props) {
    const [state, dispatch] = useReducer(dashboardReducer, initialState)

    // update profile info
    const handleUpdate = async (password) => {
        try {
            const idToken = props.idToken
            await updatePwd(password, idToken)
            dispatch({ type: 'update_success' })
        } catch (error) {
            dispatch({ type: 'update_failed', payload: error.message })
        }
    }
    return(
        <div className="container text-left pt-4">
            <h2>Dashboard</h2>
            <h4>Profile Info.</h4>
            <p>{props.user}</p>
            <p className="text-success">{state.successMsg}</p>
            {state.isOpen ? (<UpdatePwd 
                handleUpdate={handleUpdate} 
                errorMsg={state.errorMsg}
                dispatch={dispatch} />
            ) : (
                <Button onClick={() => dispatch({type: 'open_form'})}>Update Password</Button>
            )}
        </div>
    )
    
}